.container {
    margin: 0 5%;
    position: relative;
    max-width: 100%;
    height: 80vh;
    text-align: center;
    font-size: 20px;
    padding-bottom: 2%;
    grid-auto-flow: dense;
}

.container h1 {
    color: #ffffff;
}

.container a {
    text-decoration: underline;
    font-weight: bold;
    color: #ffffff;
}

.box {
    /* background-color: grey; */
    display: grid;
    height: 100%;
    place-items: center;
    text-align: center;
    grid-template-columns: 45% 45%;
    grid-column-gap: 10%;
}

.box .imgBox, .box .content {
    width: 100%;
    height: 100%;
    min-height: 500px;
    display: grid;
    border-radius: 15px;
    box-shadow: 5px 5px 5px #bdbdbd;
}

.box .imgBox {
    /* background-color: red; */
    background-color: #212121;
    overflow: hidden;
    grid-template-rows: 10% 90%;
}

.box .content {
    background-color: #212121;
    padding: 10% 10%;
    line-height: 2.5;
}

.box .content p {
    color: #ffffff;
}

.detail {
    text-align: justify;
    line-height: 1.4;
}

.box .imgBox p{
    margin: auto;
    color: #ffffff;
}

.box .imgBox img{
    height: 100%;
    max-height: 100%;
    width: 100%;
    max-width: 100%;
}

@media all and (max-width: 900px){
    .box {
        margin-top: 10vh;
        grid-template-columns: 100%;
        grid-row-gap: 2%;
    }
}

@media all and (max-width: 600px){
    .container h1 {
        font-size: 2rem;
    }
    .box .imgBox {
        grid-template-rows: 20% 80%;
    }
}