
nav {
    height: 10vh;
    width: 100%;
  }
  
  label.brand, nav a .brand {
    font-size: 2em;
    font-weight: bold;
    line-height: 10vh;
    margin-left: 5%;
  }
  
  nav a .brand:hover {
    cursor: pointer;
  }
  
  nav ul {
    float: right;
    margin-right: 5%;
  }
  
  nav ul li {
    display: inline-block;
    line-height: 10vh;
    padding:  0 2rem;
  }
  
  .checkbtn {
    font-size: 30px;
    float: right;
    line-height: 10vh;
    margin-right: 40px;
    cursor: pointer;
    display: none;
  }
  
  #check {
    display: none;
  }
  
  
  @media all and (max-width: 900px){
    nav {
        top: 0;
        position: fixed;
        z-index: 2;
        background-color: #fff;
    }
    .checkbtn {
        display: block;
    }
    nav ul {
        position: fixed;
        width: 150px;
        height: 100vh;
        top: 10vh;
        right: -100%;
        text-align: center;
        transition: 0.5s;
        background-color: #ffffff;
        box-shadow: 1px 15px 10px #333333;
        margin-right: 0;
        letter-spacing: 1.5px;
    }
    nav ul li {
        display: block;
    }
    nav ul li:hover {
      color: #ffffff;
      background-color: #212121;
    }
    #check:checked ~ ul {
        right: 0;
        z-index: 1;
    }
  }