.links {
    padding-top: 10vh;
}

.liens {
    display: block;
    background-color: #212121;
    width: 300px;
    margin: 20px auto;
    padding: 10px;
    border-radius: 15px;
    color: #ffffff;
    font-size: 2rem;
    text-align: center;
    border: 5px solid #212121;
}

.liens i {
    color: #ffffff; 
}

.liens:hover {
    color: #212121;
    background-color: #ffffff;
}

.liens:hover i {
    color: #212121;
}